.nav-container{
    background:#FFEF00;
    position: fixed;
    left:0;
    top:0;
    box-shadow:0 16px 10px rgba(0,0,0,.1);
    z-index:10;
    width: 100%;
    padding:0;
    margin:0;
}

nav{
    height: 8vh;
    display: flex;
    align-items:center;
    justify-content: space-between;
}

.brand{
    display: flex;
    text-decoration: none;
    color:#000;
    align-items:center;
    padding-left: 30px;
}

.logo-name{
    font-weight: 400;
    font-size: 20px;
}

.logo-name span{
    font-weight: 800;
}


.logo{
    height:20px;
    margin-right:5px;
}

.menu-icon{
    height:20px;
    width:20px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    display: none;
}


.dagger-open:nth-child(2){
    width: 0;
}

.dagger , .dagger-open{
    height: 5px;
    width:100%;
    transition: all .2s ease-in-out;
    position: relative;
    left: 0;
    cursor: pointer;
}
.dagger:nth-child(2){
    margin:2px 0;
}

.dagger-open:nth-child(1){
    margin:2px 0;
    transform:rotate(40deg) translate(5px,6px);
}


.dagger-open:nth-child(3){
    margin:2px 0;
    transform:rotate(-40deg) translate(5px,-6px);
}

.menu-links{
    display: flex;
    justify-content: space-around;
    list-style: none;
    font-size:15px;
    font-weight: 700;
    align-items: center;
    transition: all .2s ease-in-out;
    padding-right: 30px;
}

.links{
    padding:0 25px;  
    transition: all .2s ease-in-out;
    cursor: pointer;
    text-decoration: none;
    color:inherit
}

.links:not(:last-child):after{
    content: "";
    display: block;
    width:0;
    top:5px;
    height:2px;
    background:#000;
    transition: all .2s ease-in-out;
}

.links:hover:after{
    width:100%;
}

.links:nth-child(3):hover{
    background: #000;
    color:#FFEF00;
}

.links:nth-child(3){
    border:2px solid #000;
    border-radius: 50px;
    padding:5px 7px 5px 10px;
    margin-left: 10px;
}

@media only screen and (max-width: 768px) {
.brand{
    padding-left: 20px;
}

.menu-icon {
    display: flex;
    padding-right:20px;
}

.menu-links{
    flex-direction: column;
    position: fixed;
    justify-content: unset;
    left:-100%;
    width:100%;
    height: 100%;
    top: 8vh;
    background: #2C2D2D;
    padding:20px 0;
    margin: 0;
    color:#fff;
    opacity: 0;
    
}

.menu-links.open{
    opacity: 1;
    left:0;
}

.links{
    padding:25px;
}

.links:nth-child(3){
    border-color: #fff;
}

.links:hover:after{
    width:0;
}

}
.hero-container{
    text-align:center;
    height:92vh;
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index:-10;
}

.hero-title{
    font-size: 70px;
    font-weight: 800;
    margin:0;
}

.hero-subtitle{
    font-size: 30px;
}

.scroll-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    height:40px;
    width:20px;
    border: 5px solid #000;
    border-radius: 50px;
}

.scroll-icon div{
    height: 10px;
    width:10px;
    background: #000;
    border-radius: 50%;
    transition: all .3s ease-in-out;
    -webkit-animation: scrollDown .3s ease-in-out infinite alternate;
            animation: scrollDown .3s ease-in-out infinite alternate;
}

@-webkit-keyframes scrollDown {
    from{
        transform: translateY(-5px);
    }to{
        transform: translateY(10px);
    }
}

@keyframes scrollDown {
    from{
        transform: translateY(-5px);
    }to{
        transform: translateY(10px);
    }
}

@media screen and (max-width:930px){
    .hero-title{
